$col-xs: 300px;
$col-sm: 524px;
$col-md: 768px;
$col-lg: 992px;
$col-xl: 1170px;
$col-xxl: 1400px;

$container: (xs: 100%, sm: 100%, md: 100%, lg: 100%, xl: 136.6rem, xxl: 136.6rem);


$breakpoints: (xs: $col-xs, sm: $col-sm, md: $col-md, lg: $col-lg, xl: $col-xl, xxl: $col-xxl);
$grid-columns: 12;
$grid-gutter: 25px / 2;

$header-mobile: 1240px;

@mixin grid-col-with($key, $grid) {
  flex-basis: percentage($key/$grid);
  max-width: percentage($key/$grid); }

@mixin grid-breakpoint($point, $breakpoints) {
  @each $key, $value in $breakpoints {
    @if $point == $key {
      @media only screen and (min-width: #{$value}) {
        @content; } } } }

// stylelint-disable declaration-no-important

// Margin and Padding
$spacer: 1rem !default;
$spacers: () !default;
$sp: (0: 0,1: ($spacer * .25),2: ($spacer * .5), 3: $spacer, 4: ($spacer * 1.5),5: ($spacer * 3));
$spacers: map-merge($sp, $spacers);

@each $key, $value in $breakpoints {
  @include grid-breakpoint($key, $breakpoints) {
    @for $i from 1 through $grid-columns {

      @each $prop, $abbrev in (margin: m, padding: p) {
        @each $size, $length in $spacers {
          .#{$abbrev}-#{$key}-#{$size} {
            #{$prop}: $length !important; }
          .#{$abbrev}t-#{$key}-#{$size} {
            #{$prop}-top: $length !important; }

          .#{$abbrev}r-#{$key}-#{$size} {
            #{$prop}-right: $length !important; }

          .#{$abbrev}b-#{$key}-#{$size} {
            #{$prop}-bottom: $length !important; }

          .#{$abbrev}l-#{$key}-#{$size} {
            #{$prop}-left: $length !important; } } }

      // Negative margins (e.g., where `.mb-n1` is negative version of `.mb-1`)
      @each $size, $length in $spacers {
        @if $size != 0 {
          .m-#{$key}-n#{$size} {
            margin: -$length !important; }

          .mt-#{$key}-n#{$size} {
            margin-top: -$length !important; }

          .mr-#{$key}-n#{$size} {
            margin-right: -$length !important; }

          .mb-#{$key}-n#{$size} {
            margin-bottom: -$length !important; }

          .ml-#{$key}-n#{$size} {
            margin-left: -$length !important; }

          // Some special margin utils
          .m-#{$key}-auto {
            margin: auto !important; }
          .mt-#{$key}-auto {
            margin-top: auto !important; }

          .mr-#{$key}-auto {
            margin-right: auto !important; }

          .mb-#{$key}-auto {
            margin-bottom: auto !important; }

          .ml-#{$key}-auto {
            margin-left: auto !important; } } } } } }

// col
@each $key, $value in $breakpoints {
  @include grid-breakpoint($key, $breakpoints) {
    @for $i from 1 through $grid-columns {
      .col-#{$key}-#{$i} {
        @include grid-col-with($i, $grid-columns); }
      .order-#{$key}-#{$i} {
        order: $i; } } } }

//container
@each $key, $value in $container {
  @include grid-breakpoint($key, $breakpoints) {
    .container {
      width: $value;
      max-width: 100%; } } }

@function columnPadding($from: 1, $to: $grid-columns) {
  @if $from == $to {
    @return ',.col-xs-#{$from}, .col-sm-#{$from}, .col-md-#{$from}, .col-lg-#{$from}, .col-xl-#{$from}, .col-xxl-#{$from}'; }
  @else {
    @return '.col-xs-#{$from}, .col-sm-#{$from}, .col-md-#{$from}, .col-lg-#{$from}, .col-xl-#{$from}, .col-xxl-#{$from}, ' + columnPadding($from+1, $to); } }

#{columnPadding()} {
  padding-left: $grid-gutter;
  padding-right: $grid-gutter; }

@mixin breakpoint__xs {
  @media (min-width: $col-xs) {
    @content; } }

@mixin breakpoint__sm {
  @media (min-width: $col-sm) {
    @content; } }

@mixin breakpoint__md {
  @media (min-width: $col-md) {
    @content; } }

@mixin breakpoint__lg {
  @media (min-width: $col-lg) {
    @content; } }

@mixin breakpoint__xl {
  @media (min-width: $col-xl) {
    @content; } }

@mixin breakpoint__xxl {
  @media (min-width: $col-xxl) {
    @content; } }

@mixin breakpoint__xs--max {
  @media (max-width: $col-sm - 1px) {
    @content; } }

@mixin breakpoint__sm--max {
  @media (max-width: $col-md - 1px) {
    @content; } }

@mixin breakpoint__md--max {
  @media (max-width: $col-lg - 1px) {
    @content; } }

@mixin breakpoint__lg--max {
  @media (max-width: $col-xl - 1px) {
    @content; } }

@mixin breakpoint__header--desktop {
  @media (min-width: $header-mobile) {
    @content; } }

@mixin breakpoint__header--mobile {
  @media (max-width: $header-mobile) {
    @content; } }

@mixin style($style, $xs: null, $sm:null, $lg:null , $xl:null) {
  @if $xs == null {
    $xs: unset;
    $sm: unset;
    $lg: unset;
    $xl: unset; }
  @else if $sm == null {
    $sm: $xs;
    $lg: $xs;
    $xl: $xs; }
  @else if $lg == null {
    $lg: $sm;
    $xl: $sm; }

  @include breakpoint__xs() {
    #{$style}: $xs; }
  @include breakpoint__sm() {
    #{$style}: $sm; }
  @include breakpoint__lg() {
    #{$style}: $lg; }
  @include breakpoint__xl() {
    #{$style}: $xl; } }

@mixin col-with($width) {
  flex-basis: $width;
  max-width: $width; }

.container, .container-fluid {
  margin-left: auto;
  margin-right: auto;
  padding-left: $grid-gutter;
  padding-right: $grid-gutter; }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: - $grid-gutter;
  margin-right: - $grid-gutter;

  &--center {
    justify-content: center; }

  &.no-gutters {
    margin-left: 0;
    margin-right: 0; }

  &.align-item-center {
    align-items: center; }

  &.space-between {
    justify-content: space-between; } }

.align-self-center {
  align-self: center; }

.col {
  padding-left: $grid-gutter;
  padding-right: $grid-gutter; }

.no-gutters {
  padding-left: 0;
  padding-right: 0; }
@mixin breakpoint_dpi {
  @media (min-resolution: 120dpi) {
    @content; } }
@mixin breakpoint__min($px) {
  @media (min-width: $px) {
    @content; } }

@mixin breakpoint__max($px) {
  @media (max-width: $px) {
    @content; } }

@each $key, $value in $breakpoints {
  @include grid-breakpoint($key, $breakpoints) {
    .no-gutters-#{$key} {
      padding-left: 0;
      padding-right: 0; } } }

@each $key, $value in $breakpoints {
  @include grid-breakpoint($key, $breakpoints) {
    .has-margin-#{$key} {
      margin-left: -$grid-gutter;
      margin-right: -$grid-gutter; } } }

@each $key, $value in $breakpoints {
  @include grid-breakpoint($key, $breakpoints) {
    .d-flex-#{$key} {
      display: flex; } } }

@each $key, $value in $breakpoints {
  @include grid-breakpoint($key, $breakpoints) {
    .hide-#{$key} {
      display: none!important; } } }

@each $key, $value in $breakpoints {
  @include grid-breakpoint($key, $breakpoints) {
    .show-#{$key} {
      display: block!important; } } }

.direction-column {
  flex-direction: column; }
.direction-row {
  flex-direction: row; }

.mt-auto {
  margin-top: auto; }

.no-bottom-margin {
  margin-bottom: 0; }

